/** ------ Media Queries ------ **/
html {
  overflow-x: hidden;
  scroll-behavior: smooth;
  margin-top: 0 !important;
}

body {
  font-size: 16px;
  background-color: #F5F2EE;
}

.d-block,
picture {
  display: block;
}

.w-100, .wh-100 {
  width: 100%;
}

.h-100, .wh-100 {
  height: 100%;
}

img {
  vertical-align: top;
  height: auto;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

a,
button,
input[type=submit] {
  cursor: pointer;
  transition: all 0.3s ease;
}

main,
section,
header,
footer,
aside {
  width: 100%;
}

a {
  text-decoration: none;
}

textarea,
button,
input,
select,
option {
  outline: none;
}

video {
  display: block;
  width: 100%;
  aspect-ratio: 16/9;
}

