}
.vehicle-info .lots_live {
  position: absolute;
  left: 0;
  bottom: 0;
}
@media (min-width: 1420px) {
  .vehicle-info .lots_live {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    gap: 0.46875vw;
  }
}
@media (max-width: 1420px) {
  .vehicle-info .lots_live {
