    aspect-ratio: 1/1;
    object-fit: contain;
    object-position: center;
  }
}
@media (max-width: 1420px) {
  .vehicle-info .lots_live .dot {
    display: block;
    width: 7.2px;
    height: 7.2px;
    aspect-ratio: 1/1;
    object-fit: contain;
    object-position: center;
  }
}
.vehicle-info .lots_live p {
  color: #ffffff;
  font-family: "GothamLight";
}
.vehicle_final {
  opacity: 0;
  overflow: hidden;
  transition: all 0.5s ease;
}
@media (min-width: 1420px) {
  .vehicle_final {
    width: 40.1041666667vw;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 1.875vw;
    flex-direction: column;
  }
}
@media (max-width: 1420px) {
  .vehicle_final {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 24px;
    flex-direction: column;
  }
}
.vehicle_final > * {
  opacity: 0;
  transition: all 0.5s ease;
}
@media (min-width: 1420px) {
  .vehicle_final > * {
    transform: translateX(4.1666666667vw);
  }
}
@media (max-width: 1420px) {
  .vehicle_final > * {
    transform: translateX(50px);
  }
}
.vehicle_final h3 {
  color: #8C6E47;
  text-align: center;
  font-family: "CormorantGaramond";
  font-weight: 300;
}
@media (min-width: 1420px) {
  .vehicle_final h3 {
    font-size: 2.5vw;
    line-height: 2.5vw;
    letter-spacing: -0.075vw;
    max-width: 28.90625vw;
  }
}
.vehicle_final img {
  width: 100%;
  height: auto;
  margin-inline: auto;
}
@media (min-width: 1420px) {
  .vehicle_final img {
    max-width: 26.3541666667vw;
  }
}
@media (min-width: 1420px) {
  .vehicle.active {
    width: 33.2291666667vw;
  }
}
.vehicle.active .vehicle_title {
  opacity: 0;
  pointer-events: none;
}
.vehicle.active .vehicle_bg::before {
  opacity: 0.6;
}
.vehicle.active .vehicle_bg img {
  max-height: 100%;
}
.vehicle.active .vehicle-logo {
  z-index: 1;
}
.vehicle.active .vehicle_bottom {
  bottom: 0;
}
.vehicle.active .vehicle-info h2,
.vehicle.active .vehicle-info ul,
.vehicle.active .vehicle-info .flex {
  opacity: 1;
  pointer-events: all;
}
@media (min-width: 1420px) {
  .vehicle.active .vehicle-info .lots_live {
    bottom: calc(100% + 1.6666666667vw);
  }
}
@media (max-width: 1420px) {
  .vehicle.active .vehicle-info .lots_live {
    bottom: calc(100% + 24px);
  }
}
.vehicle.active .vehicle-info .lots_live .dot {
  background-color: #08AA2B;
}
.vehicle.active + .vehicle_final {
  opacity: 1;
}
.vehicle.active + .vehicle_final > * {
  opacity: 1;
  transform: none;
}

